@import "../../Desing/variables.scss";

.ring {
  display: inline-block;
  width: 40px;
  height: 40px;
}
.ring:after {
  content: " ";
  display: block;
  width: 40px;
  height: 40px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid rgb(250, 250, 250);
  border-color: $mainColor transparent $mainColor transparent;
  animation: ring 1.2s linear infinite;
}
@keyframes ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
